.MainButtonAnimation-enter,
 .MainButtonAnimation-appear{
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-transition: -webkit-transform 130ms ease-out;
  transition: -webkit-transform 130ms ease-out;
  -o-transition: transform 130ms ease-out;
  transition: transform 130ms ease-out;
  transition: transform 130ms ease-out, -webkit-transform 130ms ease-out;
}


.MainButtonAnimation-enter-active,
.MainButtonAnimation-appear-active {
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
}


.MainButtonAnimation-leave {
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  -webkit-transition: -webkit-transform 130ms ease-out;
  transition: -webkit-transform 130ms ease-out;
  -o-transition: transform 130ms ease-out;
  transition: transform 130ms ease-out;
  transition: transform 130ms ease-out, -webkit-transform 130ms ease-out;
}


.MainButtonAnimation-leave-active {
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
}



