.PreviewAnimation-enter {
  opacity: 0;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  -o-transition-property: transform, opacity;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: 200ms;
       -o-transition-duration: 200ms;
          transition-duration: 200ms;
  -webkit-transition-timing-function: var(--cubic-in), linear;
       -o-transition-timing-function: var(--cubic-in), linear;
          transition-timing-function: var(--cubic-in), linear;
}


.PreviewAnimation-enter-active {
  opacity: 1;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
}


.PreviewAnimation-leave {
  opacity: 1;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  -o-transition-property: transform, opacity;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: 200ms;
       -o-transition-duration: 200ms;
          transition-duration: 200ms;
  -webkit-transition-timing-function: var(--cubic-out), linear;
       -o-transition-timing-function: var(--cubic-out), linear;
          transition-timing-function: var(--cubic-out), linear;
}


.PreviewAnimation-leave-active {
  opacity: 0;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}



